.NavBarProfile{
    box-sizing: border-box;
    height: 100vh;
    background: #3a48f5;;
    width: 300px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    padding: 20px 0;
    color: white;
    font-size: 18px;
font-weight: 400;
line-height: 21px;
    

}
.nav{
    
    border-bottom: 1px solid white;
    padding: 20px 0;
  

}
.nav li{
    padding: 20px;
    color: white;
    font-size: 18px;
font-weight: 400;
line-height: 21px;
cursor: pointer;
    

}
.logOut a{
    padding: 20px;
    color: white;
    font-size: 18px;
font-weight: 400;
line-height: 21px;
cursor: pointer; 
}
.profileLogo{
    padding:  50px 0;
    cursor: pointer;

    
}

.profileLogo p{
    color: white;
    font-size: 15px;
font-weight: 400;
line-height: 21px;
}
